const ru = "ru";
const en = "en";
const language = { ru: "ru-RU", en: "en-US" };
const alphabet = {
  ru: "".split(""),
  en: "abcdefghijklmnopqrstuvwxyz".split(""),
};
function checkLanguage(key) {
  return language[checkLang(key)];
}
function checkLang(key) {
  return "".indexOf(
    key[0][0][0][0].toLowerCase()
  ) > -1
    ? ru
    : en;
}

